$bgcolor : #202124;
$textcolor : #e8eaed;
$disablecolor : #9aa0a6;
$bordercolor : #5f6368;

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  color: $textcolor;
  outline: none;
  border: none; 
}

body{
  background: $bgcolor;
  padding: 20px;
  height: 100vh;
  position: relative;
}

.App{
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
}
.Title{
  margin-bottom: 20px;
}

.wrap-list{
  width: 100%;
  border: 1px solid $bordercolor;
  border-radius: 25px;
  padding: 20px;
  min-height: 60vh;
}

.confirmed-disable{
  display: none;
  visibility: hidden;
  position: fixed;
  height: 50px;
  width: 200px;
  top: 50%;
  left: 50%;
  justify-content: space-around;
  transform: translate(-50%, -50%);
}
.confirmed-active{
  display: flex;
  visibility: visible;
  position: fixed;
  height: 50px;
  width: 200px;
  top: 50%;
  left: 50%;
  justify-content: space-around;
  transform: translate(-50%, -50%);
}
