$bgcolor : #202124;
$textcolor : #e8eaed;
$disablecolor : #9aa0a6;
$bordercolor : #5f6368;

.todo-item{
    min-height: 30px;
    display: flex;
    margin-bottom: 20px;
}

.wrap-input{
    min-width: 5%;
    padding-top: 5px;
    margin-right: 20px;
    .input-checkbox{
        cursor: pointer;
        width: 16px;
        height: 16px;
    }
}

.wrap-description{
    width: 90%;
    .description{
        font-size: 18px;
    }
    .active{
        color: $textcolor;
        text-decoration: none;
    }
    .disable{
        color: $disablecolor;
        text-decoration: line-through;
    }
}

.wrap-delete{
    display: block;
    height: 20px;
    width: 20px;
    padding-top: 5px;
    margin-left: 20px;
    .close{
        cursor: pointer;
        position: relative;
        width: 20px;
        height: 20px;
        background: none;
        border: none;
    }
    .close:before, .close:after {
        position: absolute;
        content: ' ';
        top: 0;
        height: 20px;
        width: 2px;
        background-color: #333;
    }
    .close:before {
    transform: rotate(45deg);
    }
    .close:after {
    transform: rotate(-45deg);
    }
}