$bgcolor : #202124;
$textcolor : #e8eaed;
$disablecolor : #9aa0a6;
$bordercolor : #5f6368;

.InputDescription{
    margin-bottom: 20px;
    display: flex;
    min-height: 30px;
    height: auto;
    border: 1px solid $bordercolor;
    border-radius: 25px;
    padding: 20px;
    justify-content: space-between;
}


.text-input{
    overflow-y: visible;
    display: block;
    width: 80%;
    min-height: 50px;
    height: auto;
    background: none;
    font-size: 18px;
    margin-right: 5%;
    resize: vertical;
}

button{
    display: block;
    width: 70px;
    height: 40px;
    text-align: center;
    font-size: 18px;
    cursor: pointer;
    background: none;
    border: 2px solid $bordercolor;
    border-radius: 25px;
}